"use client"

import type React from "react"
import { useState } from "react"
import { Button } from "@/components/ui/button"
import { FileText, Check, AlertCircle, Plus } from "lucide-react"
import { Progress } from "@/components/ui/progress"

export function DocumentUpload() {
  const [files, setFiles] = useState<File[]>([])
  const [uploading, setUploading] = useState(false)
  const [progress, setProgress] = useState(0)
  const [uploadStatus, setUploadStatus] = useState<"idle" | "success" | "error">("idle")

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length > 0) {
      const selectedFiles = Array.from(e.target.files).filter((file) => file.type === "application/pdf")
      setFiles(selectedFiles)
    }
  }

  const handleUpload = async () => {
    if (files.length === 0) return

    setUploading(true)
    setProgress(0)

    // Simulate upload progress
    const interval = setInterval(() => {
      setProgress((prev) => {
        if (prev >= 95) {
          clearInterval(interval)
          return 95
        }
        return prev + 5
      })
    }, 200)

    try {
      await new Promise((resolve) => setTimeout(resolve, 2000))
      clearInterval(interval)
      setProgress(100)
      setUploadStatus("success")

      setTimeout(() => {
        setFiles([])
        setProgress(0)
        setUploadStatus("idle")
      }, 3000)
    } catch (error) {
      clearInterval(interval)
      setUploadStatus("error")
    } finally {
      setUploading(false)
    }
  }

  return (
    <div className="space-y-3">
      <div className="text-center">
        <label htmlFor="file-upload-sidebar" className="cursor-pointer">
          <div className="flex flex-col items-center p-4 border-2 border-dashed border-blue-300 rounded-lg hover:border-blue-400 hover:bg-blue-50 transition-colors">
            <Plus className="h-6 w-6 text-blue-600 mb-2" />
            <span className="text-sm font-medium text-blue-600">Add Documents</span>
            <span className="text-xs text-gray-500 mt-1">PDF files only</span>
          </div>
          <input
            id="file-upload-sidebar"
            type="file"
            multiple
            accept=".pdf"
            className="sr-only"
            onChange={handleFileChange}
            disabled={uploading}
          />
        </label>
      </div>

      {files.length > 0 && (
        <div className="space-y-2">
          <div className="text-xs font-medium text-gray-700">Selected ({files.length}):</div>
          <div className="max-h-20 overflow-y-auto space-y-1">
            {files.map((file, index) => (
              <div key={index} className="flex items-center text-xs bg-gray-50 p-2 rounded">
                <FileText className="h-3 w-3 mr-1 flex-shrink-0 text-blue-600" />
                <span className="truncate flex-1">{file.name}</span>
              </div>
            ))}
          </div>

          <Button
            onClick={handleUpload}
            disabled={uploading}
            size="sm"
            className="w-full bg-blue-600 hover:bg-blue-700 text-white"
          >
            {uploading ? "Processing..." : "Upload"}
          </Button>
        </div>
      )}

      {(uploading || progress > 0) && (
        <div className="space-y-1">
          <div className="flex justify-between text-xs">
            <span>Processing...</span>
            <span>{progress}%</span>
          </div>
          <Progress value={progress} className="h-1" />
        </div>
      )}

      {uploadStatus === "success" && (
        <div className="p-2 bg-green-50 border border-green-200 rounded flex items-center text-green-700 text-xs">
          <Check className="h-3 w-3 mr-1" />
          <span>Success!</span>
        </div>
      )}

      {uploadStatus === "error" && (
        <div className="p-2 bg-red-50 border border-red-200 rounded flex items-center text-red-700 text-xs">
          <AlertCircle className="h-3 w-3 mr-1" />
          <span>Error occurred</span>
        </div>
      )}
    </div>
  )
}
